import './FilialAnalysis.scss'
import {connect} from "react-redux";
import Map from "../Map";

function FilialAnalysis(props: Props) {
// ================================= STATE =================================

// ================================= FUNCTIONS =================================

// ================================= LAYOUT =================================
  return (
    <div className="filial-analysis">
      <Map></Map>
    </div>
  );
}

// ================================= PROPS =================================

interface Props {

}

interface IMapStoreToProps {

}

interface IMapDispatchToProps {
  reduxActions: {},
}


// ================================= REDUX =================================

// Загружаем данные из store в props:
function mapStoreToProps(store: any): IMapStoreToProps {
  return {}
}

// Передаем в props объект reduxActions, который содержит в себе экшны для изменения store.
function mapDispatchToProps(dispatch: any): IMapDispatchToProps {
  return {
    reduxActions: {}
  }
}

// ================================= EXPORT =================================

// интерфейсы в коннекте нужны для того, чтобы в компонент не запихивать те поля, которые будут добавляться при mapStoreToProps и mapDispatchToProp
// собсна в эти интерфейсы надо запихать то, что будет в mapStoreToProps и mapDispatchToProp
// так же полезно запихать те же поля в основной интерфейс пропсов для удобства работы с ними

export default connect<IMapStoreToProps, IMapDispatchToProps>(mapStoreToProps, mapDispatchToProps)(FilialAnalysis);